import { useEffect, useMemo, useState, useRef, useCallback } from 'react';
import { focusItem } from './tree-helpers';
import { toggleExpandAllHelper } from './group-expands-helpers';

export const useInstanceRefActions = ({ actions }, ctx) => {
  const {
    props: { onItemFocus },
    virtualListHelpers,
    treeRoot,
    visibleItems,
    triggerTreeRerender,
    setSelectedItem,
    setFocusedItem,
    setLatestToggledItem,
  } = ctx;
  const { current: promises } = useRef({});
  const { scrollToIndex } = useMemo(() => virtualListHelpers, [
    virtualListHelpers,
  ]);
  const [expandPromise, setPromise] = useState();

  useEffect(() => {
    if (expandPromise && promises.expandResolve) {
      promises.expandResolve(visibleItems);
      promises.expandResolve = null;
      setPromise(null);
    }
  }, [visibleItems, expandPromise]);
  actions.toggleExpandAll = useCallback(
    (isExpand) => {
      const p = new Promise((resolve) => {
        promises.expandResolve = resolve;
        toggleExpandAllHelper(
          isExpand,
          treeRoot,
          triggerTreeRerender,
          setLatestToggledItem,
        );
      });
      setPromise(p);
      return p;
    },
    [toggleExpandAllHelper, promises, setPromise],
  );
  actions.scrollTo = useCallback(
    (indx, opts = { align: 'start' }) => {
      if (scrollToIndex) scrollToIndex(indx, opts);
    },
    [scrollToIndex],
  );

  actions.setSelectedItemByVirtualIndex = useCallback(
    (index) => {
      const itemByIndex = visibleItems[index];
      setSelectedItem(itemByIndex);
    },
    [visibleItems, setSelectedItem],
  );

  actions.setFocusedItemByVirtualIndex = useCallback(
    (index, opts = { align: 'start', withScroll: false }) => {
      const itemByIndex = visibleItems[index];
      const onItemFocusParams = {
        itemIndex: itemByIndex.virtualIndex,
        scrollToItem: () => scrollToIndex(index, opts),
        item: itemByIndex,
      };

      if (opts.withScroll && scrollToIndex) {
        scrollToIndex(index, opts);
        setTimeout(() => {
          setFocusedItem(itemByIndex);
          focusItem(itemByIndex);
          onItemFocus(onItemFocusParams);
        }, 300);
      } else {
        setFocusedItem(itemByIndex);
        focusItem(itemByIndex);
        onItemFocus(onItemFocusParams);
      }
    },
    [visibleItems, scrollToIndex, setFocusedItem, focusItem],
  );

  return actions;
};
